.widget{
    display: flex;
    flex: 1;
    justify-content: space-between;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    height: 100px;

    .left, .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .title{
        font-size: 14px;
        font-weight: bold;
        color: rgba(160, 160, 160)
    }

    .counter{
        font-weight: 300;
        font-size: 28px;
    }
    .link{
        width: max-content;
        border-bottom: 1px solid gray;
        font-size: 12px;
    }

    .percentage{
        display: flex;
        align-items: center;
        font-size: 14px;

        &.positive{
            color: green;
        }
        &.negative{
            color: red;
        }
    }
    .icon{
        font-size: 18px;
        padding: 5px;
        background-color: rgba(128, 0, 128, 0.541);
        border-radius: 5px;
        align-self: flex-end;
    }
}