.home{
    display: flex;
    .homeContainer{
         flex: 6;
         .widgets, .chart{
             display: flex;
             padding: 20px;
             gap: 20px;
         }
         .chart{
             padding: 5px 20px;
         }
         .listContainer{
            -webkit-box-shadown: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            padding: 20px;
            margin: 20px;
         }
         .listTitle{
             font-weight: 500;
             color: grey;
             margin-bottom: 15px;
         }
    }
    
}